.header {
	background-color: $white;
  &__wrapper {
    position: relative;
    height: 85px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  &__logo {
    position: absolute;
    bottom: 0;
    left: 0;
    transform: translateY(50%);
    width: 301px;
    height: 119px;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
}